.image-border {
    border-color: #000;
    border-style: solid;
    border-width: 3px;
    padding: 5px;

    width: 60px;
    height: 60px;

    @media (min-width: 480px) {
        width: 210px;
        height: 210px;
    }
}

.image {
    height: 50px;
    width: 50px;

    @media (min-width: 480px) {
        width: 200px;
        height: 200px;
    }
}
